.switch{
  .slider{
    background-color: $switch-slider--background-color;
    &:before {
      background-color: $switch-slider-before--background-color;
    }
  }
  input:checked + .slider {
    background-color: $switch-slider-checked--background-color;
  }

  input:focus + .slider {
    box-shadow: 0 0 1px $switch-slider-focus--box-shadow-color;
  }
}



