
.progressBar {
  position: absolute;
  margin: 0;
  padding: 0;
  top: 62px;
  left: 10px;
  width: calc(100% - 20px);
  height: 28px;
  z-index: 9999;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border-radius: 5px;
  overflow: hidden;
}
.progressBar progress {
    position: absolute;
    top: 0;
    left: 0;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 28px;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
}
.progressBar progress::-webkit-progress-value {
      background-image: -webkit-linear-gradient(-45deg, transparent 33%, rgba(0, 0, 0, 0.1) 33%, rgba(0, 0, 0, 0.1) 66%, transparent 66%), -webkit-linear-gradient(top, rgba(255, 255, 255, 0.25), rgba(0, 0, 0, 0.25)), -webkit-linear-gradient(left, #70b3db, #4ea1d3);
      border-radius: 2px;
      background-size: 49px 28px, 100% 100%, 100% 100%;
}
.progressBar p {
    position: absolute;
    font-size: 12px;
    line-height: 28px;
    left: 7px;
    top: 0;
    color: #fff;
    margin: 0;
    padding: 0;
}



