.url-preview {
  .content {
    color: $room-chat-urlpreview-title--color;
    background-color: $room-chat-urlpreview--background-color;
    border: {
      color: $room-chat-urlpreview--border-color;
    }
    .description {
      opacity: 0.7;
    }
    &.isVideo{
      background-color: $main-color;
      color: #FFF;
    }
  }
}

.host {
  color: $room-chat-urlpreview-host--color;
}



