$primary-color: $room-chat-primary-color;
$second-color: $room-chat-second-color;
$third-color: $room-chat-third-color;
$fourth-color: $room-chat-fourth-color;
$fifth-color: $room-chat-fifth-color;

.e {
  background-color: $room-chat-e--background-color;
  .room-chat-title{
    border-bottom: solid 1px $room-chat-e-room-chat-title--border-bottom-color;
    p{
      color: $room-chat-e-room-chat-title--color;
      .room-description{
        color: $room-chat-e-room-description--color;
      }
    }
  }
}

.room-chat {
  @import '../_scrollbar.scss';
  .disconnect{
    .disconnect-text{
      color: $room-chat-disconnect-text--color;
      background-color: $room-chat-disconnect-text--background-color;
      border: solid 2px $room-chat-disconnect-text--color;
    }
  }
  ul {
    li {
      .message {
        &.pointer .chat-msg {
          cursor: pointer;
          &.text {
            &:hover {
              filter: drop-shadow(1px 1px 1px $room-chat-ul-li-message-chat-msg-text-hover--filter-color);
            }
          }
        }
        .chat-msg {
          background: $third-color;
          color: $fifth-color;

          .msg-reply {
            background-color: $room-chat-ul-li-message-chat-msg-msg-reply--background-color;
          }

          &.fail {
            background-color: $room-chat-ul-li-message-chat-msg-fail--background-color !important;
            &::after {
              border-left-color: $room-chat-ul-li-message-chat-msg-fail-after--border-left-color !important;
            }
          }
        }
        &.chat-msg-self {
          .chat-time-readed {
            .chat-readed {
              color: $fourth-color;
            }
          }
          .chat-msg {
            background-color: $primary-color;
            color: $room-chat-ul-li-message-chat-msg-chat-msg-self-chat-msg--color;
            .msg-reply {
              background-color: $room-chat-ul-li-message-chat-msg-chat-msg-self-chat-msg-msg-reply--background-color;
              color: $room-chat-ul-li-message-chat-msg-chat-msg-self-chat-msg-msg-reply--color;
            }
          }
        }
        &.sys-msg {
          .chat-msg {
            background: $third-color;
            color: $fifth-color;
          }
        }
        .chat-time {
          color: $fourth-color;
        }
      }
    }
  }
}
.chat-input {
  border-top: 1px solid $room-chat-chat-input--border-top-color;
  .input-text{
    textarea{
      color: $room-chat-chat-input-textarea--color;
      background-color: $room-chat-chat-input-textarea--background-color;
      &::placeholder {
        color: $room-chat-chat-input-textarea-placeholder--color;
        opacity: 1;
      }
      &:-ms-input-placeholder {
        color: $room-chat-chat-input-textarea-placeholder--color;
      }
      &::-ms-input-placeholder {
        color: $room-chat-chat-input-textarea-placeholder--color;
      }
    }
  }
}

.msg-side {
  .chat-sender {
    color: $room-chat-msg-side-chat-sender--color;
  }
}

.timespan {
  background-color: $room-chat-timespan--background-color;
  color: $room-chat-timespan--color;
}

.btn-send {
  color: $room-chat-btn-send--color;
  background-color: $primary-color;
}

.dragDialog {
  border: 5px dashed $room-chat-dragDialog--border-color;
  background-color: $room-chat-dragDialog--background-color;
}

i.fa {
  fill: $second-color;
}



