
.v-spinner{
  position: relative;
  top: calc(50% - 12.5px);
}
.v-spinner .v-beat {
  -webkit-animation: v-beatStretchDelay 2s infinite linear;
  animation: v-beatStretchDelay 2s infinite linear;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  display: inline-block;
}
.v-spinner .v-beat-odd {
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}
.v-spinner .v-beat-even {
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}
@-webkit-keyframes v-beatStretchDelay {
50% {
    -webkit-transform: scale(0.75);
    transform: scale(0.75);
    -webkit-opacity: 0.2;
    opacity: 0.2;
}
100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-opacity: 1;
    opacity: 1;
}
}
@keyframes v-beatStretchDelay {
50% {
    -webkit-transform: scale(0.75);
    transform: scale(0.75);
    -webkit-opacity: 0.2;
    opacity: 0.2;
}
100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-opacity: 1;
    opacity: 1;
}
}



