.room-list {
  background-color: $room-list--background-color;
  border-right: solid thin $room-list--border-right-color;
  .room-list-title{
    border-bottom: solid 1px $room-list-title--border-bottom-color;
    p{
      color: $room-list-title--color;
    }
  }
  .room-search {
    border-bottom: solid 1px $room-list-room-search--border-bottom-color;
    .room-search-input{
      color: $room-list-room-search-input--color;
      background-color: $room-list-room-search-input--background-color;
      border: solid 1px $room-list-room-search-input--border-color;
      &::placeholder {
        color: $room-list-room-search-input-placeholder--color;
        opacity: 1;
      }
      &:-ms-input-placeholder {
        color: $room-list-room-search-input-placeholder--color;
      }
      &::-ms-input-placeholder {
        color: $room-list-room-search-input-placeholder--color;
      }
    }
    .btn{
      i{
        stroke: $room-list-room-search-btn-i--stroke-color;
      }
    }
  }
  ul {
    @import '../_scrollbar.scss';
    li {
      border-bottom: 1px solid $room-list-ul-li--border-bottom-color;
      .room-left{
        .room-name-last{
          .room-title{
            color: $room-list-ul-li-room-left-room-title--color;
            .room-description{
              color: $room-list-description--color;
            }
          }
          .room-last{
            color: $room-list-ul-li-room-left-room-last--color;
          }
        }
      }
      .room-right{
        .room-time{
          color: $room-list-ul-li-room-right-room-time--color;
        }
        .room-badge{
          background-color: $room-list-ul-li-room-right-room-badge--background-color;
          color: $room-list-ul-li-room-right-room-badge--color;
        }
      }
      &:hover{
        background-color: $room-list-ul-li-hover--background-color;
      }
      &.active{
        background-color: $room-list-ul-li-active--background-color;
      }
    }
  }
}



