.showloc {
  border: {
    color: $room-chat-showloc--border-color;
  }
  color: $room-chat-showloc--color;
  background-color: $room-chat-showloc--background-color;
}

.address {
  .title {
    color: $room-chat-showloc-address-title--color;
  }
  .description {
    color: $room-chat-showloc-address-description--color;
  }
}



