.image-swiper{
  background-color: $room-chat-image-swiper--background-color;
  .swiper-container {
    .swiper-arrow{
      fill: $room-chat-image-swiper-arrow--color;
    }
  }
  .gallery-thumbs {
    border-top: solid thin $room-chat-image-swiper-gallery-thumbs--border-color;
    .swiper-slide {
      .download{
        .downloadIcon{
          background-color: rgba($room-chat-image-swiper--background-color,.5);
          fill: $room-chat-image-swiper-arrow--color;
        }
      }
    }
  }
}



