&:not(d){
  background-color: rgba($scrollbar-thumb--color, 0);
  -webkit-background-clip: text;
  transition: background-color .3s;
}
&::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
&::-webkit-scrollbar-button {
  width: 0px;
  height: 0px;
}
&::-webkit-scrollbar-thumb {
  background-color: inherit;
  border: 0px none $scrollbar-thumb--color;
  border-radius: 4px;
  min-height: 32px;
  &:hover{
    background-color: inherit;
  }
  &:active{
    background-color: inherit;
  }
}
&::-webkit-scrollbar-track {
  background: $scrollbar-track--color;
  border: 0px none $scrollbar-track--color;
  border-radius: 4px;
  &:hover{
    background: $scrollbar-track--color;
  }
  &:active{
    background: $scrollbar-track--color;
  }
}
&::-webkit-scrollbar-corner {
  background: transparent;
}
&:hover {
  @media screen and (-webkit-min-device-pixel-ratio:0) {
    background-color: rgba($scrollbar-thumb--color, 1);
  }
}



