
.chat-reply {
  padding: 6px 34px 6px 12px;
  bottom: 0;
  width: 100%;
  opacity: 0;
  visibility: hidden;
  position: absolute;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-transition: .2s;
  transition: .2s;
  border-radius: 5px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  left: 0;
  height: 50px;
  overflow-x: hidden;
  font-size: 14px;
}
.chat-reply .content {
    vertical-align: middle;
    text-overflow: ellipsis;
    overflow: hidden;
    font-size: 12px;
    max-width: 100%;
}
.chat-reply.active {
    bottom: 75px;
    opacity: 1;
    visibility: visible;
}
.chat-reply .reply-close {
    cursor: pointer;
    position: absolute;
    right: 12px;
    top: 20%;
}
.chat-reply .reply-close:after {
      line-height: 30px;
      font-size: 30px;
      vertical-align: middle;
      content: '\d7';
}



