.room-info {
  border-left: solid thin $room-info--border-right-color;
  background-color: $room-info--background-color;
  .room-info-title{
    border-bottom: solid 1px $room-info-title--border-bottom-color;
    p{
      color: $room-info-title--color;
    }
  }
  .room-info-date{
    border-bottom: solid 1px $room-info-title--border-bottom-color;
    color: $room-info-date--color;
  }
  .room-info-members{
    .room-info-members-count{
      color: $room-info-members-count--color;
    }
    .room-info-members-list{
      @import '../_scrollbar.scss';
      .room-info-member{
        border-bottom: $room-info-member--border-color solid thin;
        &:hover{
          background-color: $room-info-member-hover--background-color;
        }
        .room-info-name{
          color: $room-info-member-name--color;
        }
        .room-info-edit{
          .room-info-edit-icon{
            fill: $room-info-member-edit-icon--fill-color;
          }
        }
      }
    }
  }
  .room-info-edit{
    .room-info-edit-icon{
      fill: $room-info-edit-icon--fill-color;
    }
    .room-info-edit-down{
      border: $room-info-edit-down--border-color solid thin;
      background-color: $room-info-edit-down--background-color;
      color: $room-info-edit-down--color;
      .room-info-edit-down-item{
        &:hover{
          color: $room-info-edit-down-item-hover--color;
        }
      }
    }
  }
}




