
* {
  font-family: "Helvetica Neue","Microsoft JhengHei", Helvetica,Arial,sans-serif;
}
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  height: 100%;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: left;
      -ms-flex-pack: left;
          justify-content: left;
}
body {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
html {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}
audio {
  width: 200px;
}
.loading {
  height: calc(100% - 50px);
  padding-top: 50%;
}
.btn-m {
  border: none;
  border-radius: 4px;
  display: inline-block;
  line-height: 30px;
  padding: 0 10px;
  vertical-align: middle;
  text-decoration: none;
  text-align: center;
  -webkit-transition: .2s ease-out;
  transition: .2s ease-out;
  cursor: pointer;
  font-size: 14px;
}
.btn-m .disabled {
    pointer-events: none;
    cursor: default;
}
.input-m {
  border: none;
  border-radius: 4px;
  outline: none;
  font-size: 14px;
  margin: 0 0 20px 0;
  padding: 5px 12px;
  -webkit-box-shadow: none;
          box-shadow: none;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.waves-effect {
  position: relative;
  cursor: pointer;
  display: inline-block;
  overflow: hidden;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  vertical-align: middle;
  z-index: 1;
  -webkit-transition: .3s ease-out;
  transition: .3s ease-out;
}
.room-info-trigger {
  fill: black !important;
  position: fixed;
  top: 16px;
  right: 16px;
  cursor: pointer;
}
@media screen and (min-width: 801px) {
#app > #list {
    width: 300px;
}
#app > #chat {
    width: 100%;
}
#app > #info {
    width: 300px;
}
#app.showList > #chat {
    width: calc(100% - 300px);
}
#app.showInfo > #chat {
    width: calc(100% - 300px);
}
#app.showList.showInfo > #chat {
    width: calc(100% - 600px);
}
}
@media screen and (max-width: 800px) and (min-width: 701px) {
.room-info-trigger {
    display: none;
}
#app > #list {
    width: 300px;
}
#app > #chat {
    width: 100%;
}
#app > #info {
    display: none;
}
#app.showList > #chat {
    width: calc(100% - 300px);
}
}
@media screen and (max-width: 700px) {
.room-info-trigger {
    display: none;
}
#app > #list {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
}
#app > #chat {
    width: 100%;
}
#app > #info {
    display: none;
}
#app.showList .showListBtn {
    display: block !important;
}
#app.showChat > #list {
    left: -100%;
}
}



