
.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: table;
  -webkit-transition: opacity .3s ease;
  transition: opacity .3s ease;
}
.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}
.modal-container {
  width: 600px;
  max-width: 80vw;
  margin: 0px auto;
  padding: 13px;
  border-radius: 7px;
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
  position: relative;
}
.modal-header div {
  margin-top: 0;
  font-size: 16px;
  font-weight: bold;
}
.modal-body {
  /*margin: 20px 0;*/
  max-width: 80vw;
  /*max-height: 100%;*/
}
.modal-default-button {
  float: right;
}
.close {
  position: absolute;
  right: 16px;
  top: 16px;
}

/*
 * The following styles are auto-applied to elements with
 * transition="modal" when their visibility is toggled
 * by Vue.js.
 *
 * You can easily play with the modal transition by editing
 * these styles.
 */
.modal-enter {
  opacity: 0;
}
.modal-leave-active {
  opacity: 0;
}
.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}



