&{
  color: $app--color;
  background-color: $app--background-color;
  border: solid thin $app--border-color;
}
.btn-m{
  color: $app-btn-m--color;
  background-color: $app-btn-m--background-color;
  &:hover {
    background-color: $app-btn-m-hover--background-color;
  }
}
.danger {
  background-color: $app-danger--background-color;
  &:hover {
    background-color: $app-danger-hover--background-color;
  }
}
.input-m {
  background-color: $app-input-m--background-color;
  border: 1px solid $app-input-m--border-color;
}
.waves-effect {
  -webkit-tap-highlight-color: $app-waves-effect--tap-highlight-color;
}

@import 'components/_BeatLoader.scss';
@import 'components/_Modal.scss';
@import 'components/_Switch.scss';
@import 'components/RoomChat/_avatar.scss';
@import 'components/RoomChat/_emoji.scss';
@import 'components/RoomChat/_imageSwiper.scss';
@import 'components/RoomChat/_loadComp.scss';
@import 'components/RoomChat/_recorder.scss';
@import 'components/RoomChat/_reply.scss';
@import 'components/RoomChat/_RoomChat.scss';
@import 'components/RoomChat/_showLoc.scss';
@import 'components/RoomChat/_sticker.scss';
@import 'components/RoomChat/_urlPreview.scss';
@import 'components/RoomInfo/_RoomInfo.scss';
@import 'components/RoomList/_RoomList.scss';



