
.showloc {
  border-width: 1px;
  border-style: solid;
  border-radius: 8px;
  width: 240px;
  overflow: hidden;
}
.address {
  padding: 8px 12px;
  font-size: 14px;
}
.address .title {
    text-overflow: ellipsis;
    overflow: hidden;
}
.address .description {
    text-overflow: ellipsis;
    overflow: hidden;
}
.vue-map {
  border-radius: 8px 8px 0 0;
  overflow: hidden;
}
.vue-map div:first-child {
    border-radius: 6px 6px 0 0;
}



