.recorder {
  box-shadow: 0 0 0 1px rgba($room-chat-recorder--box-shadow-color, .1), 0 1px 10px rgba($room-chat-recorder--box-shadow-color, .35);
  background-color: $room-chat-recorder--background-color;
  .timestamp {
    color: $room-chat-recorder-timestamp--color;
  }
  a {
    color: $room-chat-recorder-a--color;
  }

  .recorder-btn {
    background-color: $room-chat-recorder-recorder-btn--background-color;
    color: $room-chat-recorder-recorder-btn--color;
  }

  .btn-title.active {
    background-color: $room-chat-recorder-btn-title-active--background-color;
  }

  .frequency {
    background-color: $room-chat-recorder-frequency--background-color;
  }

  .cancel {
    color: $room-chat-recorder-cancel--color;
  }
}



