.loadComp{
  .svg-icon path,
  .svg-icon polygon,
  .svg-icon rect {
    fill: $room-chat-loadComp-svg-icon--color;
  }

  .svg-icon circle {
    stroke: $room-chat-loadComp-svg-icon--color;
  }

  .chatimg {
    border: {
      color: $room-chat-loadComp-chatimg-border--color;
    }
  }

  .comment {
    border-left: 5px solid $room-chat-loadComp-comment--border-left-color;
    color: $room-chat-loadComp-comment--color;
  }
}



