
.url-preview.chat-msg-self .content {
  margin-left: auto;
  margin-right: 0px;
}
.url-preview .content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 60px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  text-decoration: none;
  padding: 5px 8px;
  text-align: left;
  width: 301px;
  max-width: 100%;
  position: relative;
  margin-left: 38px;
  margin-bottom: 5px;
  border-style: solid;
  border-width: 1px;
  border-radius: 8px;
}
.url-preview .content .left {
    overflow: hidden;
    width: 60px;
    height: 60px;
    margin-right: 7px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 4px;
}
.url-preview .content .right {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.url-preview .content .right .description {
      word-break: break-all;
      display: -webkit-box;
      overflow: hidden;
      text-overflow: ellipsis;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      margin-top: 4px;
      max-height: 32px;
      line-height: 16px;
      font-size: 12px;
      font-weight: normal;
}
.url-preview .content .right .title {
      font-size: 12px;
      font-weight: normal;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
}
.url-preview .content .right .title a {
        text-decoration: none;
}
.url-preview .content.isVideo {
    width: 200px;
    height: 185px;
    display: block;
    padding: 0;
}
.url-preview .content.isVideo .left {
      width: 100%;
      height: 0;
      padding-bottom: 60%;
      margin-right: 0;
      margin-bottom: 5px;
      position: relative;
      fill: rgba(255, 255, 255, 0.6);
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
}
.url-preview .content.isVideo .left .play {
        position: absolute;
        left: calc(50% - 24px);
        top: calc(50% - 24px);
}
.url-preview .content.isVideo .right {
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      padding: 0 8px 8px 8px;
}
.host {
  font-size: 9px;
}



