.sticker-box {
  background-color: $room-chat-sticker--background-color;
  box-shadow: 0 0 0 1px rgba(0,0,0,.1), 0 1px 5px rgba(0,0,0,.25);
  .box-body {
    @import '../_scrollbar.scss';
  }
  .box-title {
    background-color: $room-chat-sticker-box-title--background-color;
    color: $room-chat-sticker-box-title--color;
    .box-close {
      &:hover {
        color: $room-chat-sticker-box-close--color;
      }
    }
  }

  .mask {
    background-color: $room-chat-sticker-mask--background-color;
  }
}



